.container{
    background-color: #121212;
    color: #fff;
    padding: 100px 0 50px 0;
    line-height: 1.7;
}

.projects{
    margin-top: 125px;
    
}

.tech{
    margin: 15px 0;
    display: flex;
    gap: 15px;
    align-items: center;
    justify-content: center;
}

.btn{
    background: #d40202 !important;
box-shadow: 0px 0px 4px rgba(69, 123, 157, 0.8), 0px 0px 8px rgba(69, 123, 157, 0.26);
border-radius: 4px;
color: rgb(255, 255, 255) !important;
padding: 5px 10px !important;
display: flex;
gap: 10px;
}

.nba .btn{
    background: #1702d4 !important;
box-shadow: 0px 0px 4px rgba(69, 123, 157, 0.8), 0px 0px 8px rgba(69, 123, 157, 0.26);
border-radius: 4px;
color: rgb(255, 255, 255) !important;
padding: 5px 10px !important;
display: flex;
gap: 10px;
}

.ttt .btn{
    background: #1702d4 !important;
box-shadow: 0px 0px 4px rgba(69, 123, 157, 0.8), 0px 0px 8px rgba(69, 123, 157, 0.26);
border-radius: 4px;
color: rgb(255, 255, 255) !important;
padding: 5px 10px !important;
display: flex;
gap: 10px;
}

.btnLink{
    background: #fff  !important;
box-shadow: 0px 0px 4px rgba(69, 123, 157, 0.8), 0px 0px 8px rgba(69, 123, 157, 0.26);
border-radius: 4px;
color: #000 !important;
padding: 8px 20px !important;



}

.links{
    
    display: flex;
    gap: 20px;
}

.links a{
    text-decoration: none;
}

.btnLink a {
    text-decoration: none !important;
}



.btnIcon{
    margin-top: 5px;

}



.headlineBorder{
    border-bottom: 7px solid #d40202;
    width: 5%;
    border-radius: 10px;
    border-width: 50%;
    margin: 0 auto;
    margin-bottom: 20px;
}

.formula{

    width: 80%;
    margin: 0 auto;
    display: flex;
    height: 55vh;
    background-color: #5b0303;
    
    
}

.nba{
    width: 80%;
    margin: 0 auto;
    display: flex;
    height: 55vh;
    background-color: #03165b;
}

.wow{
    width: 80%;
    margin: 0 auto;
    display: flex;
    height: 55vh;
    background-color: #2a1212;
}

.ttt{
    width: 80%;
    margin: 0 auto;
    display: flex;
    height: 55vh;
    background-color: #060251;
}

.formulaInfo{
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.formulaInfo{
    padding:30px 0;
}

.paragraph{
    width: 80%;
    margin: 0 auto;
}


